@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff");
}
.page {
  background-color: #ffffff;
  font-family: "PT Sans", sans-serif;
}

.page__body {
  margin: 0;
}

.button {
  background-color: transparent;
}

.icon {
  background-color: transparent;
}

.help-text {
  color: rgba(51, 51, 51, 0.6);
  font-size: 12px;
  line-height: 21px;
}

.help-text--capital {
  text-transform: uppercase;
}

.main-header {
  background-color: #ffffff;
}

.main-nav {
  background-color: #ffffff;
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.main-nav__item {
  background-color: #81b3d2;
}

.main-nav__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
}

.main-container {
